<template>
  <template v-if="selectOptions">
    <div class="payplugUISelect _show"
         :id="id"
         :class="optionsClassAttribute"
         :data-e2e-name="(selectData) ? escape(selectData) : null">
      <div class="_current" :tabindex="(selectDisabled) ? '1' : null">
        <div v-for="option in selectOptions" class="_value" :key="option?.key">
          <input
              class="_input"
              type="radio"
              :id="(selectName) + '-' + escape(option?.key)"
              :value="escape(option?.key)"
              :name="escape(selectName)"
              :data-e2e-name="escape(selectName) + '-' + escape(option?.key)"
              :checked="(defaultValue == option?.key) ? 'checked' : null">
          <span class="_text">{{ escape(option?.value) }}</span>
        </div>
      </div>
      <div class="_listWrapper" :class="(selectScrollbar) ? '-scrollbar' : ''">
        <div class="_list">
          <ul>
            <li v-for="option in selectOptions" :key="'label_' + option?.key">
              <label class="_option"
                     @click="selectTheOption(option?.key)"
                     :for="(selectName) + '-' + escape(option?.key)"
                     aria-hidden="aria-hidden">{{ escape(option?.value) }}</label>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </template>
</template>

<script>
import {myMixin} from "../../../mixins"
import $ from "jquery"

export default {
  name: "PayplugSelect",
  mixins: [myMixin],
  data: () => {
    return {
      defaultValue: false,
      id: (new Date()).getTime()
    }
  },
  props: {
    selectOptions: {
      type: Object,
      required: true
    },
    selectName: {
      type: String,
      required: true
    },
    selectClassName: {
      type: String,
      required: false
    },
    selectDisabled: {
      type: String,
      required: false
    },
    selectData: {
      type: String,
      required: false
    },
    selectScrollbar: {
      type: String,
      required: false
    },
  },
  mounted() {
    this.$props.selectOptions.forEach((option) => {
      if (option?.selected && !this.defaultValue) {
        this.defaultValue = option.key
      }
    })
    if (!this.defaultValue) {
      this.defaultValue = this.$props.selectOptions[0].key
    }
    var comp = this
    $(document).on("click", "._current", function () {
      $("#" + comp.id).toggleClass("collapse")
    })
  },
  computed: {
    optionsClassAttribute: function () {
      var classAttribute = ""
      if (this.$props?.selectClassName)
        classAttribute += this.escape(this.$props.selectClassName) + " "
      if (this.$props?.selectDisabled)
        classAttribute += "-disabled"

      return classAttribute
    },
  },
  methods: {
    selectTheOption(key) {
      this.defaultValue = key
    }
  }
}
</script>

<style scoped>
.payplugUISelect.collapse ._listWrapper {
  overflow: inherit !important;
}
.payplugUISelect ._listWrapper ._list{
  border: 1px solid #c8d7e4;
  z-index: 999;
}
.payplugUISelect ._listWrapper ._list ._option, .payplugUISelect ._listWrapper ._list ._option:hover{
  z-index: 999;
}
.bootstrap .payplugUISelect ._option:hover {
  background: #defff5;
}
</style>