<template>
  <section class="payplugUiBlock" :class="blockClassAttribute"
           :data-e2e-name="(blockData) ? escape(blockData) : null">
    <h2 v-if="blockTitle" class="_title">
      {{ escape(blockTitle) }}
    </h2>
    <p v-if="blockDescription" class="_description">
      {{ escape(blockDescription) }}
    </p>
    <div v-if="blockContent" class="_content" v-html="blockContent"></div>
    <div v-else class="_content"><slot/></div>
  </section>
</template>

<script>
import {myMixin} from "../../../mixins"

export default {
  name: "PayplugBlock",
  mixins: [myMixin],
  props: {
    blockClassName: {
      type: String,
      required: false
    },
    blockDisabled: {
      type: String,
      required: false
    },
    blockData: {
      type: String,
      required: false
    },
    blockTitle: {
      type: String,
      required: false
    },
    blockDescription: {
      type: String,
      required: false
    },
    blockContent: {
      type: String,
      required: true,
      default: ""
    },
  },
  computed: {
    blockClassAttribute: function () {
      var classAttribute = ""
      if (this.$props?.blockClassName)
        classAttribute += this.escape(this.$props.blockClassName) + " "
      if (this.$props?.blockDisabled)
        classAttribute += "-disabled"

      return classAttribute
    },
  }
}
</script>

<style scoped>

._content {
  min-height: 320px;
}
</style>