<template>
  <div class="payplugUIInput" :class="inputClassAttribute" v-if="this.$props">
    <label v-if="inputLabel" :for="escape(inputName)">{{escape(inputLabel)}}</label>
    <input
        :type="(inputType) ? escape(inputType) : null"
        :id="escape(inputName)"
        :name="escape(inputName)"
        :data-e2e-name="(inputData) ? escape(inputData) : null"
        :value="(inputValue) ? escape(inputValue) : null"
        :placeholder="(inputPlaceholder) ? escape(inputPlaceholder) : null"
        :step="(inputType=='number' && inputStep) ? escape(inputStep) : null"
        :min="(inputType=='number' && inputMin) ? escape(inputMin) : null"
        :max="(inputType=='number' && inputMax) ? escape(inputMax) : null"
        :disabled="(inputDisabled) ? 'disabled' : null"
        autocomplete="new-password"
        @change="valueChanged" @input="valueChanged">
  </div>
</template>

<script>
import {myMixin} from "../../../mixins"

export default {
  name: "PayplugInput",
  mixins: [myMixin],
  props: {
    inputName: {
      type: String,
      required: true,
      default: ""
    },
    inputType: {
      type: String,
      required: true,
      default: "text"
    },
    inputClassName: {
      type: String,
      required: false
    },
    inputIcon: {
      type: String,
      required: false
    },
    inputDisabled: {
      type: String,
      required: false
    },
    inputLabel: {
      type: String,
      required: false
    },
    inputData: {
      type: String,
      required: false
    },
    inputValue: {
      type: String,
      required: false
    },
    inputPlaceholder: {
      type: String,
      required: false
    },
    inputStep: {
      type: String,
      required: false
    },
    inputMin: {
      type: String,
      required: false
    },
    inputMax: {
      type: String,
      required: false
    },
  },
  computed: {
    inputClassAttribute: function () {
      var classAttribute = ""
      if (this.$props?.inputClassName)
        classAttribute += this.escape(this.$props.inputClassName) + " "
      if (this.$props?.inputIcon)
        classAttribute += "-icon -icon" + this.escape(this.$props.inputIcon) + " "
      if (this.$props?.buttonDisabled)
        classAttribute += "-disabled"

      return classAttribute
    },
  },
  methods: {
    valueChanged(e) {
      this.$emit('inputChanged', e.target.value)
    }
  }
}
</script>

