<template>
  <div class="payplugUIIOptions"
       :class="escape(optionsClassName)">
    <template v-for="item in items" :key="item.value">
      <label :class="escape(item?.className) + ' ' + (item?.disabled ? '-disabled' : '')"
             :data-e2e-name="(item?.dataName) ? escape(item?.dataName) : null">
        <input
            type="radio"
            :name="escape(optionsName)"
            :value="escape(item?.value)"
            :checked="(optionsSelected && item?.value && optionsSelected == item?.value) ? 'checked' : null"
            :disabled="(item?.disabled) ? 'disabled' : null"
            :data-notallowed="(item?.notallowed) ? escape(item?.notallowed) : 0"/>
        <span>
          {{ escape(item?.text) }}
          <span v-if="item?.subText">{{ escape(item?.subText) }}</span>
            </span>
      </label>
    </template>
  </div>
</template>

<script>
import {myMixin} from "../../../mixins"

export default {
  name: "PayplugOptions",
  mixins: [myMixin],
  props: {
    items: {
      type: Object,
      required: true
    },
    optionsName: {
      type: String,
      required: true
    },
    optionsClassName: {
      type: String,
      required: false
    },
    optionsSelected: {
      type: String,
      required: false
    },

  }
}
</script>

