<template>
  <a class="payplugUILink" :class="linkClassAttribute"
     :href="(linkHref) ? escape(linkHref) : null"
     :target="(linkTarget) ? escape(linkTarget) : '_blank'"
     :title="linkText"
     :data-e2e-name="(linkData) ? escape(linkData) : null"
     :disabled="(linkDisabled) ? 'disabled' : null">
    {{ escape(linkText) }}
  </a>
</template>

<script>
import {myMixin} from "../../../mixins"

export default {
  name: "PayplugLink",
  mixins: [myMixin],
  props: {
    linkText: {
      type: String,
      required: false,
      default: ""
    },
    linkClassName: {
      type: String,
      required: false
    },
    linkDisabled: {
      type: String,
      required: false
    },
    linkHref: {
      type: String,
      required: false
    },
    linkTarget: {
      type: String,
      required: false
    },
    linkData: {
      type: String,
      required: false
    },
  },
  computed: {
    linkClassAttribute: function () {
      var classAttribute = ""
      if (this.$props?.linkClassName)
        classAttribute += this.escape(this.$props.blockClassName) + " "
      if (this.$props?.linkDisabled)
        classAttribute += "-disabled"

      return classAttribute
    },
  }
}
</script>

<style scoped>
.payplugUILink.-disabled {
  pointer-events: none;
}
</style>