      <span class="_slider"></span>
      <span v-if="switchEnabledLabel"
            class="_label"
            :data-enable-text="(switchEnabledLabel) ? escape(switchEnabledLabel) : null"
            :data-disable-text="(switchDisabledLabel) ? escape(switchDisabledLabel) : null"></span>
    </label>
  </div>
