import {myMixin} from "../../../mixins"

export default {
  name: "PayplugSwitch",
  mixins: [myMixin],
  props: {
    switchName: {
      type: String,
      required: false
    },
    switchClassName: {
      type: String,
      required: false
    },
    switchDataName: {
      type: String,
      required: false
    },
    switchDisabled: {
      type: String,
      required: false
    },
    switchChecked: {
      type: String,
      required: false
    },
    switchEnabledLabel: {
      type: String,
      required: false
    },
    switchDisabledLabel: {
      type: String,
      required: false
    },
  }
}
</script>

