<template>
  <p class="payplugUIParagraph" :class="paragraphClassAttribute">
    {{escape(paragraphText)}}
  </p>
</template>

<script>
import {myMixin} from "../../../mixins"

export default {
  name: "PayplugParagraph",
  mixins: [myMixin],
  props: {
    paragraphClassName: {
      type: String,
      required: false
    },
    paragraphDisabled: {
      type: String,
      required: false
    },
    paragraphText: {
      type: String,
      required: true,
      default: ""
    },
  },
  computed: {
    paragraphClassAttribute: function () {
      var classAttribute = ""
      if (this.$props?.paragraphClassName)
        classAttribute += this.escape(this.$props.paragraphClassName) + " "
      if (this.$props?.paragraphDisabled)
        classAttribute += "-disabled"

      return classAttribute
    },
  }
}
</script>

