<template>
  <div class="payplugUIAccordion" :class="accordionClassAttribute"
       :data-e2e-name="(accordionData) ? escape(accordionData) : null">
    <input type="checkbox" :id="escape(accordionIdentifier)">
    <label :for="escape(accordionIdentifier)" @click="show = !show">{{ escape(accordionLabel) }}</label>
    <div v-if="show" class="payplugUIAccordion_contentWrapper">
      <div v-if="accordionContent" class="payplugUIAccordion_content">{{ accordionContent }}</div>
      <div v-else class="payplugUIAccordion_content">
        <slot/>
      </div>
    </div>
  </div>
</template>

<script>
import {myMixin} from "../../../mixins"

export default {
  name: "PayplugAccordion",
  mixins: [myMixin],
  data: () => {
    return {
      show: false
    }
  },
  props: {
    accordionLabel: {
      type: String,
      required: false,
      default: ""
    },
    accordionIdentifier: {
      type: String,
      required: true,
      default: ""
    },
    accordionContent: {
      type: String,
      required: true,
      default: ""
    },
    accordionClassName: {
      type: String,
      required: false
    },
    accordionData: {
      type: String,
      required: false
    },
  },
  computed: {
    accordionClassAttribute: function () {
      var classAttribute = ""
      if (this.$props?.paragraphClassName)
        classAttribute += this.escape(this.$props.paragraphClassName) + " "

      return classAttribute
    },
  }

}
</script>

<style scoped>
.bootstrap .payplugUIAccordion > label {
  width: calc(100% - 72px - 24px);
}

.payplugUIAccordion_contentWrapper {
  width: calc(100% - 72px);
}
</style>