<template>
  <a :href="escape(buttonLinkHref)"
     :title="(buttonLinkTitle ? escape(buttonLinkTitle) : escape(buttonLinkText))"
     :target="escape(buttonLinkTarget)"
     class="payplugUIButtonLink"
     :class="buttonLinkClassAttribute"
     :disabled="(buttonLinkDisabled) ? 'disabled' : null"
     :data-e2e-name="(buttonLinkData) ? escape(buttonLinkData) : null">
    <template v-if="buttonLinkIcon">
      <Icon :iconName="buttonLinkIcon"/>
    </template>
    {{ escape(buttonLinkText) }}
  </a>
</template>

<script>
import {myMixin} from "../../../mixins"
import Icon from "@/components/atoms/icon/Icon"

export default {
  name: "PayplugButtonLink",
  components: {
    Icon
  },
  mixins: [myMixin],
  props: {
    buttonLinkHref: {
      type: String,
      required: true,
      default: ""
    },
    buttonLinkTitle: {
      type: String,
      required: false
    },
    buttonLinkText: {
      type: String,
      required: false
    },
    buttonLinkTarget: {
      type: String,
      required: false,
      default: "_blank"
    },
    buttonLinkData: {
      type: String,
      required: false
    },
    buttonLinkClassName: {
      type: String,
      required: false
    },
    buttonLinkStyle: {
      type: String,
      required: false
    },
    buttonLinkIcon: {
      type: String,
      required: false
    },
    buttonLinkDisabled: {
      type: String,
      required: false
    },
  },
  computed: {
    buttonLinkClassAttribute: function () {
      var classAttribute = ""
      if (this.$props?.buttonLinkClassName)
        classAttribute += this.escape(this.$props.buttonLinkClassName) + " "
      if (this.$props?.buttonLinkStyle)
        classAttribute += " -" + this.escape(this.$props.buttonLinkStyle) + " "
      if (this.$props?.buttonLinkIcon)
        classAttribute += "-icon "
      if (this.$props?.buttonLinkDisabled)
        classAttribute += "-disabled"

      return classAttribute
    },
  }
}
</script>

