<template>
  <div class="payplugUIIcon" :class="iconClassAttribute"
       :data-e2e-name="(iconData) ? escape(iconData) : null">
    <img :src="require('../../../assets/img/svg/icons/' + iconName + '.svg')" :alt="iconName"/>
  </div>
</template>

<script>
import {myMixin} from "../../../mixins"
import arrow_down from "../../../assets/img/svg/icons/arrow_down.svg"
import check from "../../../assets/img/svg/icons/check.svg"
import close from "../../../assets/img/svg/icons/close.svg"
import error from "../../../assets/img/svg/icons/error.svg"
import euro_symbol from "../../../assets/img/svg/icons/euro_symbol.svg"
import lightbulb from "../../../assets/img/svg/icons/lightbulb.svg"
import link from "../../../assets/img/svg/icons/link.svg"
import lock from "../../../assets/img/svg/icons/lock.svg"
import timer from "../../../assets/img/svg/icons/timer.svg"
import tooltip from "../../../assets/img/svg/icons/tooltip.svg"

export default {
  name: "PayplugIcon",
  mixins: [myMixin],
  data: () => {
    return {
      arrow_down: arrow_down,
      check: check,
      close: close,
      error: error,
      euro_symbol: euro_symbol,
      lightbulb: lightbulb,
      link: link,
      lock: lock,
      timer: timer,
      tooltip: tooltip,
    }
  },
  props: {
    iconName: {
      type: String,
      required: true,
      default: "close"
    },
    iconClassName: {
      type: String,
      required: false
    },
    iconData: {
      type: String,
      required: false
    },
  },
  computed: {
    iconClassAttribute: function () {
      var classAttribute = ""
      if (this.$props?.iconClassName)
        classAttribute += this.escape(this.$props.iconClassName) + " "

      return classAttribute
    },
    theIcon: function () {
      return "../../../assets/img/svg/icons/" + this.$props.iconName + ".svg"
    },
  }
}
</script>

