<template>
  <div class="payplugUITitle"
       :class="titleClassAttribute">
    {{ escape(titleText) }}
  </div>
</template>

<script>
import {myMixin} from "../../../mixins"

export default {
  name: "PayplugTitle",
  mixins: [myMixin],
  props: {
    titleText: {
      type: String,
      required: true,
      default: ""
    },
    titleClassName: {
      type: String,
      required: false
    },
    titleDisabled: {
      type: String,
      required: false
    },
  },
  computed: {
    titleClassAttribute: function () {
      var classAttribute = ""
      if (this.$props?.titleClassName)
        classAttribute += this.escape(this.$props.titleClassName) + " "
      if (this.$props?.titleDisabled)
        classAttribute += "-disabled"

      return classAttribute
    },
  }
}
</script>

<style scoped>
.payplugUITitle.text-left {
  text-align: left !important;
}
</style>