<template>
  <button type="button"
          :name="buttonName"
          class="payplugUIButton"
          :class="buttonClassAttribute"
          :data-e2e-name="(buttonData) ? escape(buttonData) : null">
    {{ escape(buttonText) }}
  </button>
</template>

<script>
import {myMixin} from "../../../mixins"

export default {
  name: "PayplugButton",
  mixins: [myMixin],
  props: {
    buttonName: {
      type: String,
      required: false
    },
    buttonClassName: {
      type: String,
      required: false
    },
    buttonStyle: {
      type: String,
      required: false
    },
    buttonDisabled: {
      type: String,
      required: false
    },
    buttonText: {
      type: String,
      required: false
    },
    buttonData: {
      type: String,
      required: false
    },
  },
  computed: {
    buttonClassAttribute: function () {
      var classAttribute = ""
      if (this.$props?.buttonClassName)
        classAttribute += this.escape(this.$props.buttonClassName) + " "
      if (this.$props?.buttonStyle)
        classAttribute += this.escape(this.$props.buttonStyle) + " "
      if (this.$props?.buttonDisabled)
        classAttribute += "-disabled"

      return classAttribute
    },
  }
}
</script>

