<template>
  <div class="payplugUIModal" :id="modalID">
    <label class="_overlay" for="modalTriggered" @click="closeModal"></label>
    <div class="_modal"
         :class="modalClassAttribute"
         :data-e2e-name="(modalData) ? escape(modalData) : null">
      <label for="modalTriggered" class="_close" @click="closeModal">
        <Icon iconName="close"/>
      </label>
      <div v-if="modalTitle" class="_header">{{escape(modalTitle)}}</div>
      <div v-if="modalContent" class="_body" v-html="modalContent"></div>
      <div v-else class="_body">
        <slot/>
      </div>
    </div>
  </div>
</template>

<script>
import {myMixin} from "../../../mixins"
import Icon from "@/components/atoms/icon/Icon"
import $ from "jquery"

export default {
  name: "PayplugModal",
  components: {
    Icon
  },
  mixins: [myMixin],
  props: {
    modalID: {
      type: String,
      required: true,
    },
    modalTitle: {
      type: String,
      required: false
    },
    modalClassName: {
      type: String,
      required: false
    },
    modalData: {
      type: String,
      required: false
    },
    modalContent: {
      type: String,
      required: true,
      default: ""
    },
  },
  computed: {
    modalClassAttribute: function () {
      var classAttribute = ""
      if (this.$props?.modalClassName)
        classAttribute += this.escape(this.$props.modalClassName) + " "
      if (!this.$props?.modalTitle)
        classAttribute += " -noTitle "

      return classAttribute
    },
  },
  methods: {
    closeModal() {
      $("#" + this.$props.modalID).removeClass("_show")
    }
  }
}
</script>

<style scoped>
.payplugUIModal._show ._overlay, .payplugUIModal._show ._modal{
  display: block !important;
}
</style>