<template>
  <Block
      blockClassName="generalBlock -login"
      :blockTitle="data_?.translations?.payplug_general_title"
      :blockDescription="data_?.translations?.payplug_general_description"
      :blockDisabled="disabled">
    <Loader v-if="loading"/>
    <form v-if="!loading" class="_loginForm">
      <div>
        <Input inputClassName="_email"
               inputName="title"
               :inputValue="title"
               :inputPlaceholder="data_?.translations?.payplug_title"
               :inputLabel="data_?.translations?.payplug_title"
               @inputChanged="titelChanged"/>
      </div>
      <div>
      <Input inputClassName="_email"
             inputName="title"
             :inputValue="description"
             :inputPlaceholder="data_?.translations?.payplug_description"
             :inputLabel="data_?.translations?.payplug_description"
             @inputChanged="descriptionChanged"/>
      </div>
      <div style="text-align: start;">
        <Button buttonName="payplug_save_changes"
                @click="saveData"
                :buttonText="data_?.translations?.payplug_save_changes"
                buttonClassName="_connexion"/>
      </div>
      <br/>
      <a @click="showData($event)" href="#" class="poc2-data-show-button">show data</a>
      <div class="poc2-data-show" v-if="show_data">
        <pre>{{ JSON.stringify(data_, null, 2) }}</pre>
      </div>
    </form>
  </Block>
</template>

<script>
import Block from "@/components/atoms/block/Block"
import Input from "@/components/atoms/input/Input"
import Button from "@/components/atoms/button/Button"
import Loader from "@/components/atoms/loader/Loader"
import $ from "jquery"
import axios from "axios"

var comp

export default {
  name: "PayplugGeneral2",
  components: {
    Block,
    Input,
    Button,
    Loader,
  },
  props: {
    loading: {
      type: Boolean,
      required: false,
      default: true
    },
    data_: {
      type: Object,
      required: false,
    },
  },
  data() {
    return {
      show_data: false,
      title: "",
      description: "",
      disabled: false
    }
  },
  mounted() {
    comp = this
  },
  updated() {
    if(this.title == "") {
      this.title= this.$props.data_?.options?.title
      this.description= this.$props.data_?.options?.description
    }
  },
  methods: {
    showData(e) {
      e.preventDefault()
      this.show_data = !this.show_data
    },
    titelChanged(value) {
      this.title = value
    },
    descriptionChanged(value) {
      this.description = value
    },
    saveData() {
      if(window.set_data_url)
        this.setDataInServer(window.set_data_url)
      else
        this.setDataInJsonFile()
    },
    setDataInServer(url) {
      this.disabled = true
      var data = new FormData();
      data.append('title', comp.title);
      data.append('description', comp.description);
      axios.post(url,
          data,
          {headers: {"Content-Type": "application/json",}}
      ).then(function (res) {
        comp.openCloseModalx()
        comp.disabled = false
        console.clear()
        console.log(res.data)
      })
    },
    setDataInJsonFile() {
      this.disabled = true
      setTimeout(function () {
        comp.openCloseModalx()
        comp.disabled = false
      }, 5000)
    },
    openCloseModalx() {
      if($("#modalx").hasClass("_show")) {
        $("#modalx").removeClass("_show")
        return
      }
      $("#modalx").addClass("_show")
    }
  }
}
</script>

<style scoped>
.poc2-data-show {
  background-color: #eee;
  overflow-x: auto;
  border: 1px solid #d5d5d5;
  box-shadow: 3px 3px 8px -2px rgba(0,0,0,0.47);
  padding: 5px  20px;
  margin-top: 10px !important;
  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box;    /* Firefox, other Gecko */
  box-sizing: border-box;
}
.poc2-data-show, .poc2-data-show pre {
  text-align: start;
  display: block !important;
}
.poc2-data-show-button {
  text-align: end;
  font-size: 80%;
  color: rgba(182, 143, 143, 0.53);
  font-style: italic;
  cursor: pointer;
}
</style>