export const myMixin = {
    methods: {
        escape: function (htmlStr = "") {
            if(htmlStr === "")
                return ""
            return htmlStr.toString().replace(/&/g, "&amp;")
                .replace(/</g, "&lt;")
                .replace(/>/g, "&gt;")
                .replace(/"/g, "&quot;")
                .replace(/'/g, "&#39;")
        },
    },
}

