<template>
  <div class="payplugUIAlert" :class="alertClassAttribute"
       :data-e2e-name="(alertData) ? escape(alertData) : null">
    <template v-if="alertClose">
      <input type="checkbox" name="alertTriggered" id="alertTriggered" />
      <label for="alertTriggered" class="_close" >
        <Icon iconName="close"/>
      </label>
    </template>
    <div v-if="alertIcon" class="_icon">
      <Icon :iconName="alertIcon"/>
    </div>
    <div v-if="alertTitle" class="_title">{{escape(alertTitle)}}  </div>
    <div class="_content" v-html="alertContent"></div>
  </div>
</template>

<script>
import {myMixin} from "../../../mixins"
import Icon from "@/components/atoms/icon/Icon"

export default {
  name: "PayplugAlert",
  components: {
    Icon
  },
  mixins: [myMixin],
  props: {
    alertType: {
      type: String,
      required: true,
      default: "success"
    },
    alertContent: {
      type: String,
      required: true,
      default: ""
    },
    alertClassName: {
      type: String,
      required: false,
    },
    alertData: {
      type: String,
      required: false,
    },
    alertClose: {
      type: String,
      required: false,
    },
    alertIcon: {
      type: String,
      required: false,
    },
    alertTitle: {
      type: String,
      required: false,
    },

  },
  computed: {
    alertClassAttribute: function () {
      var classAttribute = "-" + this.$props?.alertType + " "
      if (this.$props?.alertClassName)
        classAttribute += this.escape(this.$props.alertClassName) + " "

      return classAttribute
    },
  }
}
</script>

