<script>
import "../assets/less/admin.less"
import $ from "jquery"
import Button from "@/components/atoms/button/Button"
import ButtonLink from "@/components/atoms/buttonLink/ButtonLink"
import Input from "@/components/atoms/input/Input"
import General from "@/components/molecules/General"
import General2 from "@/components/molecules/General2"
import Link from "@/components/atoms/link/Link"
import Paragraph from "@/components/atoms/paragraph/Paragraph"
import Accordion from "@/components/atoms/accordion/Accordion"
import Alert from "@/components/atoms/alert/Alert"
import Loader from "@/components/atoms/loader/Loader"
import Image from "@/components/atoms/image/Image"
import Modal from "@/components/atoms/modal/Modal"
import Options from "@/components/atoms/options/Options"
import Select from "@/components/atoms/select/Select"
import Switch from "@/components/atoms/switch/Switch"
import TextAlert from "@/components/atoms/textAlert/TextAlert"
import Title from "@/components/atoms/title/Title"
import Tooltip from "@/components/atoms/tooltip/Tooltip"
import jsonData from "../data/data.json"
import axios from "axios"

export default {
  name: 'PayplugUi',
  components: {
    General,
    General2,
    Button,
    ButtonLink,
    Input,
    Link,
    Paragraph,
    Accordion,
    Alert,
    Loader,
    Image,
    Modal,
    Options,
    Select,
    Switch,
    TextAlert,
    Title,
    Tooltip,
  },
  props: {
    msg: String
  },
  data: () => {
    return {
      options_items: [
        {value: 1, text: "Option 1"},
        {value: 2, text: "Option 2"},
        {value: 3, text: "Option 3", subText: "subtext for option 3"},
        {value: 4, text: "Option 4", disabled: "true", subText: "disabled option"},
      ],
      select_items: [
        {key: 1, value: "Select Option 1"},
        {key: 2, value: "Select Option 2", selected: "selected"},
        {key: 3, value: "Select Option 3"},
        {key: 4, value: "Select Option 4"},
      ],
      loading_general: true,
      data_: {}
    }
  },
  methods: {
    openCloseModal1() {
      if($("#modal1").hasClass("_show")) {
        $("#modal1").removeClass("_show")
        return
      }
      $("#modal1").addClass("_show")
    },
    openCloseModal2() {
      if($("#modal2").hasClass("_show")) {
        $("#modal2").removeClass("_show")
        return
      }
      $("#modal2").addClass("_show")
    },
    openCloseModal3() {
      if($("#modal3").hasClass("_show")) {
        $("#modal3").removeClass("_show")
        return
      }
      $("#modal3").addClass("_show")
    },
    getDataFromJsonFile() {
      console.log("======> get data from json file")
      this.data_ = jsonData
      this.loading_general = false
    },
    getDataFromServer(url) {
      console.log("======> get data from server")
      var comp = this
      axios.get(url)
          .then(function (res) {
            comp.data_ = res.data
          })
          .then(function () {
            comp.loading_general = false
          });
    }
  },
  mounted() {
    var comp = this
    this.loading_general = true
    setTimeout(() => {
      console.clear()
      if(window.get_data_url)
        comp.getDataFromServer(window.get_data_url)
      else
        comp.getDataFromJsonFile()
    }, 3000)
  }
}
</script>

